body {
  font-size: 1.5em;
}

a,
pre {
  all: unset;
}

a {
  color: blue;
}

pre {
white-space: pre-wrap;
}

img {
  height: auto;
  width: auto;
  max-width: 100%;
  max-height: 100vh;  
}

.canvasWrapper {
  z-index: -1;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.canvas {
  max-height: 100vh;
  max-width: 100vw;
}

.scrubinfo {
  position: sticky;
  top:0;
}